<template>
  <div id="middle-wrapper" v-show="this.$store.state.showOption.show">
    <ul id="option-wrapper">
      <li>
        <select v-model="message">
          <option v-for="(item, index) in strings" :key="index"
          :value="item.content">
            {{ item.val }}
          </option>
        </select>
      </li>
      <li>
      <!-- color -->
      </li>
      <li>
        <label>{{ fonts[this.nums].information.defaultOptions.fontSize }}</label>
        <input type="range" name="sizeRange" min="1" max="90" v-model="fonts[1].information.defaultOptions.fontSize">
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    num: {
      type: Number,
      default: ' ',
      required: true
    }
  },
  computed: {
    fonts (){
      return this.$store.state.fonts
    },
    nums (){
      return this.$props.num
    },
    strings (){
      return this.fonts[this.nums].information.stringArr
    },
    weights (){
      return this.fonts[this.nums].information.weightArr
    },
    message: {
      get () {
        return this.fonts[this.nums].information.defaultOptions.text
      },
      set (value) {
        this.$store.commit('textVmodel', {
          value: value,
          num: this.nums
        })
      }
    },
    weight: {
      get () {
        return this.fonts[this.nums].information.defaultOptions.fontWeight
      },
      set (value) {
        this.$store.commit('weightVmodel', {
          value: value,
          num: this.nums
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../styles/setup/variables.scss";
  @import "../styles/components/range.scss";
  @import "../styles/components/selectBox.scss";

  ul {
    width: 100%;
    height: 2rem;
    display: flex;
    li {
      flex-direction: row;
      margin-left: 1.65%;
      margin-right: 1.65%;
      border-right: 1px solid $base-color;
    }
  }
</style>


